body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  font-size: 14px;
  padding: 0;
  font-family:  Verdana, sans-serif;
  box-sizing: border-box;
}

body {
  background-color: #E2E5EE;
}



.nav {
  display: flex;
  justify-content: center;
}

.nav button {
  font-size: 17px;
}

.button-add {
  position: fixed;
  left: 20px;
  top: 50%;
  background-color: #0275D8;
  border: none;
  padding: 10px;
  color: #fff;
  border-radius: 5px;
}

h1 {
  font-size: 18px;
  font-weight: normal;
}

ul {
  list-style: none;
  text-align: center;
  max-width: 1170px;
  margin: 10px auto;
  display: flex;
  flex-wrap: wrap;
  /* width: 50%; */
}

ul li {
  padding: 5px;
  /* background-color: #2a47a5; */
  /* color: #fff; */
  width: calc(50% - 20px);
  margin: 0 10px 10px 10px;
  border-radius: 5px;
  box-shadow: 0 2px 3px -3px #333;
}

input {
  padding: 5px;
  border: none;
  text-align: center;
  outline: none;
  border-bottom: 1px solid #c9c9c9;
  margin: 0 5px;
}

.button-update {
  background-color: #007ACC;
  color: #fff;
  width: 100px;
  margin: 0 auto 10px auto;
}

input {
  margin-bottom: 25px;
}

.button-delete {
  width: 100px;
  background-color: #cc0055;
  margin: 0 auto 10px auto;
  color: #fff;
}

ul li {
  background-color: #fff;
}

li img {
  display: block;
  max-width: 100%;
  /* width: 50%; */
}

.nav {
  margin: 10px 0;
}

.nav button {
  border-radius: 3px;
  border: none;
  background-color: #fff;
  cursor: pointer;
  color: #555;
  margin: 0 5px;
  padding: 5px 10px;
}

ul li {
  display: flex;
  align-items: flex-start;
  text-align: center;

  /* flex-direction: column; */

}

ul li > div {
  display: flex;
  flex-direction: column;
}


ul li span {
  padding: 5px;
}

li button {
  margin-left: auto;
  border: none;
  background-color: #fff;
  color: #555;
  padding: 10px;
  border-radius: 5px;

}

.year {
  /* color: #ccc7c7; */
  font-weight: bold;
}

